"use client";

import { products } from "@/lib/products";
import { ProductCard } from "@/components/product-card";
import { Instagram, MessageCircle, Globe, ShieldCheck } from "lucide-react";

export default function Home() {
  return (
    <main className="min-h-screen bg-background text-foreground flex flex-col md:flex-row">
      {/* Left Sidebar - Profile & Info */}
      <aside className="w-full md:w-1/4 lg:w-1/5 p-6 md:fixed md:h-screen border-r border-border flex flex-col items-center text-center bg-card/50 backdrop-blur-sm z-10">
        <div className="mb-6 mt-10">
          <div className="w-32 h-32 rounded-full bg-gradient-to-br from-neutral-800 to-neutral-600 flex items-center justify-center mb-4 mx-auto shadow-lg border-2 border-neutral-700">
             {/* Placeholder for Profile Pic */}
             <span className="text-4xl">ðŸ‘‘</span>
          </div>
          <h1 className="text-2xl font-bold mb-2">Loids Vendors</h1>
          <div className="flex items-center justify-center gap-1 text-sm text-green-500 mb-4">
             <ShieldCheck size={16} />
             <span className="font-semibold">Verified Reseller</span>
          </div>
          <p className="text-sm text-neutral-500 dark:text-neutral-400 mb-6">
            Access the exact suppliers that verify and test products. 
            High quality, passing serials, and fast shipping.
          </p>
        </div>

        <div className="flex gap-4 mb-8">
          <button className="p-2 rounded-full bg-neutral-200 dark:bg-neutral-800 hover:bg-neutral-300 dark:hover:bg-neutral-700 transition-colors">
            <Instagram size={20} />
          </button>
          <button className="p-2 rounded-full bg-neutral-200 dark:bg-neutral-800 hover:bg-neutral-300 dark:hover:bg-neutral-700 transition-colors">
            <MessageCircle size={20} />
          </button>
          <button className="p-2 rounded-full bg-neutral-200 dark:bg-neutral-800 hover:bg-neutral-300 dark:hover:bg-neutral-700 transition-colors">
            <Globe size={20} />
          </button>
        </div>

        <div className="mt-auto mb-4 text-xs text-neutral-500">
          &copy; {new Date().getFullYear()} Loids Vendors
        </div>
      </aside>

      {/* Right Content - Product Grid */}
      <div className="flex-1 md:ml-[25%] lg:ml-[20%] p-6 md:p-10">
        <div className="max-w-5xl mx-auto">
          <header className="mb-8 flex justify-between items-center">
            <h2 className="text-xl font-semibold">Available Vendors</h2>
            <div className="text-sm text-neutral-500">
              {products.length} Products
            </div>
          </header>

          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
            {products.map((product) => (
              <ProductCard key={product.id} product={product} />
            ))}
          </div>
        </div>
      </div>
    </main>
  );
}

