"use client";

import Image from "next/image";
import { Product } from "@/lib/products";
import { Check } from "lucide-react";
import { useState } from "react";
import { CheckoutModal } from "./checkout-modal";

interface ProductCardProps {
  product: Product;
}

export function ProductCard({ product }: ProductCardProps) {
  const [isModalOpen, setIsModalOpen] = useState(false);

  return (
    <>
      <div 
        className="group bg-card border border-border rounded-xl overflow-hidden hover:shadow-xl transition-all duration-300 cursor-pointer flex flex-col h-full"
        onClick={() => setIsModalOpen(true)}
      >
        <div className="relative w-full h-48 bg-neutral-100 dark:bg-neutral-800">
          <Image
            src={product.image}
            alt={product.name}
            fill
            className="object-contain p-4 group-hover:scale-105 transition-transform duration-300"
          />
        </div>
        
        <div className="p-5 flex flex-col flex-grow">
          <div className="flex justify-between items-start mb-2">
            <h3 className="font-bold text-lg">{product.name}</h3>
            <span className="font-bold text-green-600 dark:text-green-400">
              ${product.price.toFixed(2)}
            </span>
          </div>

          <div className="space-y-1 mb-4 flex-grow">
            {product.description.map((desc, index) => (
              <div key={index} className="flex items-start gap-2 text-sm text-neutral-500 dark:text-neutral-400">
                <Check size={14} className="mt-1 flex-shrink-0 text-blue-500" />
                <span>{desc}</span>
              </div>
            ))}
          </div>

          <button className="w-full py-2 bg-foreground text-background rounded-lg font-medium hover:opacity-90 transition-opacity mt-auto">
            Buy Now
          </button>
        </div>
      </div>

      {isModalOpen && (
        <CheckoutModal product={product} onClose={() => setIsModalOpen(false)} />
      )}
    </>
  );
}

